import React, { useState } from 'react';
import { Col, Form } from 'react-bootstrap';
import { toast } from 'react-toastify';
import axios from 'axios';

import Buttonstylling from '../../ButtonStyling/Buttonstyling';
import 'react-toastify/dist/ReactToastify.css';
import './dateConvert.css';

function DateConvert() {
    const [date, setDate] = useState('')
    const [type, setType] = useState('')
    const [result, setResult] = useState('Input a date')

    const submitHandler = event => {
        event.preventDefault()

        if (!date) {
            toast.error('Input a valid date')
            return
        } else if (!type) {
            toast.error('Input a valid conversion type')
            return
        }

        let dateVal = new Date(date).toISOString().split('T')[0]
        let data = new FormData(), url = 'http://localhost:8080/dateConvert'
        data.append('date', dateVal)
        data.append('type', type)

        axios.post(url, data)
            .then(res => {
                setResult(res.data)
            })
    }

    return (
        <div className='date-body'>
            <div className="headingh2">
                <h2>Workbox Utilities</h2>
                <h3>Convert Date</h3>
            </div>
            <div className="container">
                <div className="row">
                    <Col>
                        <Form.Select className="mx-auto selectbtn" style={{ fontWeight: '700' }}>
                            <option>Select date from calendar</option>
                        </Form.Select>
                        <br />
                        <Form.Group className="mx-auto col-md-6 selectbtn">
                            <Form.Control type="date" value={date} onChange={event => setDate(event.target.value)}
                                style={{ fontWeight: '500' }} />
                        </Form.Group>
                    </Col>
                    <Col xs={2} className='convert-btn ' onClick={submitHandler}>
                        <Buttonstylling style={{ marginTop: "10vh", fontSize: "1rem" }} button_content={"Convert"}></Buttonstylling>
                    </Col>
                    <Col>
                        <Form.Select className="mx-auto selectbtn" value={type}
                            onChange={event => setType(event.target.value)} style={{ fontWeight: '700' }}>
                            <option>Select Format</option>
                            <option value="MM-dd-yyyy">MM-dd-yyyy</option>
                            <option value="yyyy-MM-dd">yyyy-MM-dd</option>
                            <option value="dd MMM yyyy">dd Mon yyyy</option>
                            <option value="EEEE, MMMM dd, yyyy">Day, Mon dd, yyyy</option>
                            <option value="E, MMM dd yyyy, z">Day, Mon dd yyyy, Zone</option>
                        </Form.Select>
                        <br />
                        <Form.Group className="mx-auto col-md-8 selectbtn">
                            <Form.Control value={result} onChange={event => setResult(event.target.value)}
                                style={{ fontWeight: '500' }} disabled />
                        </Form.Group>
                    </Col>
                </div>
            </div>
        </div>
    )
}

export default DateConvert;
