import React from 'react';
import Stack from 'react-bootstrap/Stack';
import { Link } from 'react-router-dom';
import Buttonstylling from '../ButtonStyling/Buttonstyling';
import './OtherUtil.css';

function OtherUtil() {
  return (
    <div className='otherUtil'>
        <div className="headingh2">
            <h2>Workbox Utilities</h2>
            <h3>Other Utilities</h3>
        </div>

      <Stack gap={5}>
        <Link to="/currency">
          <Buttonstylling button_content={"Currency Conversion"}/>
        </Link>
        <Link to="/date">
            <Buttonstylling button_content={"Date Conversion"}/>
        </Link>
      </Stack>
      
    </div>
  );
}
export default OtherUtil;