import React, { useState } from 'react'
import { Row, Col, FloatingLabel, Form } from 'react-bootstrap';
import axios from 'axios'
import Buttonstylling from '../../ButtonStyling/Buttonstyling';
import '../Inner.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


function JsonDiff() {
    const [fromVal1, setFromVal1] = useState('')
    const [fromVal2, setFromVal2] = useState('')
    const [result, setResult] = useState('')

    const submitHandler = event => {
        event.preventDefault()

        let url = 'http://localhost:8080/jsonDiff'
        let data = new FormData()
        data.append('ljson', fromVal1)
        data.append('rjson', fromVal2)

        if (!fromVal1 || !fromVal2) {
            toast.error('Input a valid json');
            return
        }

        axios.post(url, data)
            .then(data => {
                if (data.data.startsWith('Entries'))
                    setResult(data.data)
                else {
                    toast.error('Error while parsing JSON');
                    return
                }
            })
            .catch(error => toast.error('Something went wrong!!'))
    }

    return (
        <div className='inner'>
            <div className="headingh2">
                <h2>Workbox Utilities</h2>
                <h3>JSON Difference</h3>
            </div>

            <Row>
                <Col>
                    <FloatingLabel controlId="floatingTextarea2" label="Enter First JSON">
                        <Form.Control as="textarea" placeholder="comments"
                            style={{ height: '30vh', marginBottom: '3vh', fontWeight: '500' }} value={fromVal1}
                            onChange={event => setFromVal1(event.target.value)}
                        />
                    </FloatingLabel>
                    <FloatingLabel controlId="floatingTextarea2" label="Enter Second JSON">
                        <Form.Control as="textarea" placeholder="comments"
                            style={{ height: '30vh', fontWeight: '500' }} value={fromVal2}
                            onChange={event => setFromVal2(event.target.value)}
                        />
                    </FloatingLabel>
                </Col>
                <Col xs={2} className='convert-btn' style={{ marginTop: "20vh" }} >
                    <div onClick={submitHandler}><Buttonstylling button_content={"Get Difference"} style={{ fontSize: "1rem" }} /></div>
                </Col>
                <Col>
                    <FloatingLabel controlId="floatingTextarea2" label="Output">
                        <Form.Control as="textarea"
                            style={{ height: '55vh', marginTop: '3vh', fontWeight: '500' }} value={result}
                            onChange={event => setResult(event.target.value)} disabled
                        />
                    </FloatingLabel>
                </Col>
            </Row>
        </div>
    )
}

export default JsonDiff