import React, { useState } from 'react'
import { Row, Col, InputGroup, FormControl, FloatingLabel, Form } from 'react-bootstrap';
import Buttonstylling from '../../ButtonStyling/Buttonstyling';
import '../Inner.css';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


function AddKey() {
    const [fromVal, setFromVal] = useState('')
    const [key, setKey] = useState('')
    const [value, setValue] = useState('')
    const [result, setResult] = useState('')

    const submitHandler = event => {
        event.preventDefault()
        let obj

        if (!key || !value) {
            toast.error('Input a valid key or value');
            return
        }

        try {
            obj = JSON.parse(fromVal)

            if (isNaN(value))
                obj[key] = value
            else
                obj[key] = parseFloat(value)
        } catch (error) {
            toast.error('Error while parsing JSON');
            return
        }

        setResult(JSON.stringify(obj, null, 2))
    }

    return (
        <div className='inner'>
            <div className="headingh2">
                <h2>Workbox Utilities</h2>
                <h3>Add Key to JSON</h3>
            </div>

            <Row>
                <Col>
                    <InputGroup>
                        <FormControl aria-label="Add Key" aria-describedby="basic-addon1"
                            placeholder="Add Key" value={key} onChange={event => setKey(event.target.value)}
                        />
                        <FormControl aria-label="Add Value" aria-describedby="basic-addon1"
                            placeholder="Add Value" value={value} onChange={event => setValue(event.target.value)}
                        />
                    </InputGroup>
                    <FloatingLabel controlId="floatingTextarea2" label="Enter JSON">
                        <Form.Control as="textarea" placeholder="comments"
                            style={{ height: '55vh', marginTop: '2vh', fontWeight: '500' }} value={fromVal}
                            onChange={event => setFromVal(event.target.value)}
                        />
                    </FloatingLabel>
                </Col>
                <Col xs={2} className='convert-btn' style={{ marginTop: "20vh" }} >
                    <div onClick={submitHandler}><Buttonstylling button_content={"Add Key"} style={{ fontSize: "1rem" }} /></div>
                </Col>
                <Col>
                    <FloatingLabel controlId="floatingTextarea2" label="Output">
                        <Form.Control as="textarea"
                            style={{ height: '55vh', marginTop: '3vh', fontWeight: '500' }} value={result}
                            onChange={event => setResult(event.target.value)} disabled
                        />
                    </FloatingLabel>
                </Col>
            </Row>
        </div>
    )
}

export default AddKey