import React from 'react';
import Stack from 'react-bootstrap/Stack';
import { Link } from 'react-router-dom';
import Buttonstylling from '../ButtonStyling/Buttonstyling';
import './JsonUtil.css';

function JsonUtil() {
  return (
    <div className='jsonUtil'>
        <div className="headingh2">
            <h2>Workbox Utilities</h2>
            <h3>JSON Utilities</h3>
        </div>

      <Stack gap={5}>
        <Link to="/jsonDiff">
          <Buttonstylling button_content={"JSON Difference"}/>
        </Link>
        <Link to="/addKey">
            <Buttonstylling button_content={"Add Key"}/>
        </Link>
        <Link to="/removeKey">
          <Buttonstylling button_content={"Remove Key"}/>
        </Link>
      </Stack>
      
    </div>
  );
}
export default JsonUtil;