import React, { useState } from 'react';
import { Row, Col, FloatingLabel, Form } from 'react-bootstrap';
import axios from 'axios';
import Buttonstylling from '../ButtonStyling/Buttonstyling';
import './docConvert.css';
import { toast } from 'react-toastify';

function DocConvert() {
    const [from, setFrom] = useState('')
    const [to, setTo] = useState('')
    const [fromVal, setFromVal] = useState('')
    const [toVal, setToVal] = useState('')

    const submitHandler = event => {
        event.preventDefault()
        let host = 'http://localhost:8080/', str, val
        let data = new FormData()
        data.append('formData', fromVal)

        if (from === '' || to === '' || from === to) {
            toast.error('Enter a valid conversion type');
            return
        }
        else if (from === "JSON") {
            str = host + 'json'

            try {
                val = JSON.parse(fromVal)
            } catch (error) {
                toast.error('Error while parsing JSON')
                return
            }

            if (to === "XML") {
                str = str + 'ToXml'
            } else if (to === "POJO") {
                str = str + 'ToPojo'
            }
        }
        else if (from === "XML") {
            str = host + 'xml'
            let xmlparse = new DOMParser()
            val = xmlparse.parseFromString(fromVal, "text/xml")

            if (val.querySelector("parsererror")) {
                toast.error('Error while parsing XML')
                return
            }

            if (to === "JSON")
                str = str + 'ToJson'
            else if (to === "POJO")
                str = str + 'ToPojo'
        }

        axios.post(str, data)
            .then(data => {
                if (to === "JSON") {
                    setToVal(JSON.stringify(data.data, null, 2))
                }
                else if (to === "XML") {
                    setToVal(data.data.split('><').join('>\n<'))
                }
                else if (to === "POJO") {
                    if (data.data.startsWith('Staff'))
                        setToVal(data.data.split('],').join('],\n'))
                    else {
                        let errMsg = `Error while parsing POJO. NOTE:
                                Only name, age, skills and salary are recognised fields`
                        toast.error(errMsg)
                        setToVal('')
                    }
                }
            })
            .catch(error => toast.error('Something went wrong!!'))
    }

    let label

    if (to === "POJO")
        label = "Staff name(string),age(int),skills(arraylist<string>),salary(int)"
    else
        label = "Output"

    return (
        <div className='doc-body'>
            <div className="headingh2">
                <h2>Workbox Utilities</h2>
                <h3>Convert Document</h3>
            </div>
            <Row>
                <Col>
                    <Form.Select value={from} onChange={event => { setFrom(event.target.value); setFromVal('') }}>
                        <option value="" style={{ display: 'none' }}>Select Type</option>
                        <option value="JSON" className='menu-dropdown'>JSON</option>
                        <option value="XML" className='menu-dropdown'>XML</option>
                    </Form.Select>
                    <br />
                    <FloatingLabel controlId="floatingTextarea2" label="Enter value">
                        <Form.Control as="textarea" placeholder="comments"
                            style={{ height: '55vh', marginBottom: '10vh', fontWeight: '500' }} value={fromVal}
                            onChange={event => setFromVal(event.target.value)}
                        />
                    </FloatingLabel>
                </Col>
                <Col xs={2} className='convert-btn' style={{ marginTop: "20vh" }} >
                    <div onClick={submitHandler}>
                        <Buttonstylling button_content={"Convert"} style={{ fontSize: "1rem" }} />
                    </div>
                </Col>
                <Col>
                    <Form.Select value={to} onChange={event => { setTo(event.target.value); setToVal('') }}>
                        <option value="" style={{ display: 'none' }}>Select Type </option>
                        <option value="JSON" className='menu-dropdown'>JSON</option>
                        <option value="XML" className='menu-dropdown'>XML</option>
                        <option value="POJO" className='menu-dropdown'>POJO</option>
                    </Form.Select>
                    <br />

                    <FloatingLabel controlId="floatingTextarea2" label={label}>
                        <Form.Control as="textarea"
                            style={{ height: '55vh', marginBottom: '10vh', fontWeight: '500' }} value={toVal}
                            onChange={event => setToVal(event.target.value)}
                        />
                    </FloatingLabel>
                </Col>
            </Row>
        </div>
    );
}

export default DocConvert;
