import React, { useState } from 'react';
import { Col, Form } from 'react-bootstrap';
import Buttonstylling from '../../ButtonStyling/Buttonstyling';
import Select from 'react-select';
import { toast } from 'react-toastify';

import 'react-toastify/dist/ReactToastify.css';
import './CurConvert.css';

function CurConvert() {
    const currencyArray = ['aed', 'afn', 'all', 'amd', 'ang', 'aoa',
        'ars', 'aud', 'awg', 'azn', 'bam', 'bbd',
        'bdt', 'bgn', 'bhd', 'bif', 'bmd', 'bnd',
        'bob', 'brl', 'bsd', 'btc', 'btn', 'bwp',
        'byn', 'byr', 'bzd', 'cad', 'cdf', 'chf',
        'clf', 'clp', 'cny', 'cop', 'crc', 'cuc',
        'cup', 'cve', 'czk', 'djf', 'dkk', 'dop',
        'dzd', 'egp', 'ern', 'etb', 'eur', 'fjd',
        'fkp', 'gbp', 'gel', 'ggp', 'ghs', 'gip',
        'gmd', 'gnf', 'gtq', 'gyd', 'hkd', 'hnl',
        'hrk', 'htg', 'huf', 'idr', 'ils', 'imp',
        'inr', 'iqd', 'irr', 'isk', 'jep', 'jmd',
        'jod', 'jpy', 'kes', 'kgs', 'khr', 'kmf',
        'kpw', 'krw', 'kwd', 'kyd', 'kzt', 'lak',
        'lbp', 'lkr', 'lrd', 'lsl', 'lvl', 'lyd',
        'mad', 'mdl', 'mga', 'mkd', 'mmk', 'mnt',
        'mop', 'mro', 'mur', 'mvr', 'mwk', 'mxn',
        'myr', 'mzn', 'nad', 'ngn', 'nio', 'nok',
        'npr', 'nzd', 'omr', 'pab', 'pen', 'pgk',
        'php', 'pkr', 'pln', 'pyg', 'qar', 'ron',
        'rsd', 'rub', 'rwf', 'sar', 'sbd', 'scr',
        'sdg', 'sek', 'sgd', 'shp', 'sll', 'sos',
        'srd', 'std', 'svc', 'syp', 'szl', 'thb',
        'tjs', 'tmt', 'tnd', 'top', 'try', 'ttd',
        'twd', 'tzs', 'uah', 'ugx', 'usd', 'uyu',
        'uzs', 'vef', 'vnd', 'vuv', 'wst', 'xaf',
        'xag', 'xcd', 'xdr', 'xof', 'xpf', 'yer',
        'zar', 'zmk', 'zmw', 'zwl'];

    const options = [];
    for (let curr of currencyArray) {
        var CURR = curr.toUpperCase();
        options.push({ value: CURR, label: CURR });
    }
    const [fromCurr, setfromCurr] = useState('');
    const [toCurr, settoCurr] = useState('');
    const [amount, setamount] = useState('1');
    const [result, setresult] = useState('Input Currency');

    const handleSubmit = (event) => {
        event.preventDefault();
        var apiKey = '0376a3b7c2bbee50d4bd';
        var query = fromCurr.value + '_' + toCurr.value;

        if (!fromCurr || !toCurr) {
            toast.error("Enter a valid conversion type");
            return
        }

        var url = 'https://free.currconv.com/api/v7/convert?q='
            + query + '&compact=ultra&apiKey=' + apiKey;

        fetch(url)
            .then(response => response.json())
            .then(data => {
                if (data) {
                    setresult(Math.round((amount * data[query]) * 100) / 100);
                }
            })
    }

    return (
        <div className='cur-body'>
            <div className="headingh2">
                <h2>Workbox Utilities</h2>
                <h3>Convert Currency</h3>
            </div>

            <div className="innerbox">
                <div className="COL">
                    <Select
                        defaultValue={fromCurr}
                        onChange={setfromCurr}
                        placeholder={"Select Currency"}
                        options={options}
                    />
                    <br />
                    <Form.Group className="mx-auto col-md-6">
                        <Form.Control type="number" placeholder="Enter value" value={amount}
                            onChange={(event) => setamount(event.target.value)}
                            style={{ fontWeight: '500' }} required />
                    </Form.Group>
                </div>
                <Col xs={2} className='convert-btn' onClick={handleSubmit}>
                    <Buttonstylling button_content={"Convert"} style={{ marginTop: "5vh", fontSize: "1rem" }} />
                </Col>
                <div className="COL">
                    <Select
                        defaultValue={toCurr}
                        onChange={settoCurr}
                        placeholder={"Select Currency"}
                        options={options}
                    />
                    <br />
                    <Form.Group className="mx-auto col-md-6">
                        <Form.Control type="text" value={result} style={{ fontWeight: '500' }}
                            onChange={event => setresult(event.target.value)} disabled />
                    </Form.Group>
                </div>
            </div>
        </div>

    )
}

export default CurConvert;
